import React from 'react';
import Container from "react-bootstrap/Container";
const ReturnPolicy = () => {
    return (
        <Container>
            <h3 style={{ color: "#3C3B6E" }} class="display-4"> <  br/> R E T U R N - P O L I C Y</h3>
            <p className="mb5 aboutus-page-text">You may return most new, unopened items within 30 days of delivery for a full refund. We’ll also pay the return shipping costs if the return is a result of our error (you received an incorrect or defective item, etc.).</p>
​
            <p className="mb5 aboutus-page-text">You should expect to receive your refund within four weeks of giving your package to the return shipper, however, in many cases you will receive a refund more quickly. This time period includes the transit time for us to receive your return from the shipper (5 to 10 business days), the time it takes us to process your return once we receive it (3 to 5 business days), and the time it takes your bank to process our refund request (5 to 10 business days).

            If you need to return an item, simply login to your account, view the order using the “Complete Orders” link under the My Account menu and click the Return Item(s) button. We’ll notify you via e-mail of your refund once we’ve received and processed the returned item.

            For any other query please get in contact with us on : <b>(+1)786-290-3000</b>.
            </p>
        </Container>
    )
}
export default ReturnPolicy;
